<template>
<div>
  <img :src="item.img" alt="">
  <h1 v-text='item.title'></h1>
  <div v-text='item.content'></div>
  <hr>

  <h1 v-text='$route.params.id'></h1>
  <h1 v-text='id'></h1>
</div>
</template>

<script>
import { corporateList } from '@/utils/data'
export default {
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data: function() {
    return {
      list: corporateList
    }
  },
  computed: {
    // 使用计算属性，找到指定的列表项数据
    item: function() {
      // 方式1：使用props接收动态路由参数
      let id = this.id
      // 方式2：使用this.$route接收动态路由参数
      // let id = this.$route.params.id
      return this.list[this.list.findIndex(ele=>ele.id==id)]
    }
  },
  mounted() {
    // let id = this.$route.params.id
    console.log('$route', this.$route)
    // 根据 id 来调接口，获取更多详情数据
  }
}
</script>

